/* Core */
import { Component, Input } from '@angular/core';
import { IonicPage, ViewController, NavOptions } from 'ionic-angular';

@IonicPage({
  name: 'filter-popover'
})
@Component({
  templateUrl: 'filter-popover.html'
})
export class FilterPopoverPage {
  public languages: string[];
  public langEn: boolean;
  public langFr: boolean;
  public period: string;
  public displayLanguages = true;
  public showFilterCategories: boolean;
  public filterCategories: boolean;
  constructor(public viewCtrl: ViewController) {
  }

  ngOnInit() {
    this.period = this.viewCtrl.data.period;
    this.languages = this.viewCtrl.data.languages || [];
    if (this.viewCtrl.data.displayLanguages !== undefined) {
        this.displayLanguages = this.viewCtrl.data.displayLanguages;
    }
    if (this.viewCtrl.data.filterCategories !== undefined && this.viewCtrl.data.filterCategories != null) {
        this.showFilterCategories = true;
        this.filterCategories = this.viewCtrl.data.filterCategories;
    }
    else {
        this.showFilterCategories = false;
    }
    this.langEn = this.contains('en');
    this.langFr = this.contains('fr');
  }

  onSave() {
    let that = this;
    that.languages = [];
    if (that.langEn) {
        that.languages.push('en');
    }
    if (that.langFr) {
        that.languages.push('fr');
    }
    this.viewCtrl.dismiss({
        period: that.period,
        languages: that.languages,
        filterCategories: that.filterCategories
    });
  }

  contains(lang) {
    let contained = false;
    this.languages.forEach(function(language) {
      if (language === lang) {
        contained = true;
        return;
      }
    });

    return contained;
  }
}



